<template>
    <div class="singer">
        <div class="f-index">
            <h2 v-text="findexArr[curCateIndex].findex" v-if="hasSinger"></h2>
        </div>

        <div id="singerScrollWrapper" :style="autoHeight">
            <ul>
                <li v-for="(singers) in singerList" :key="singers.Findex">
                    <h2 v-text="singers.Findex"></h2>
                    <ol>
                        <router-link tag="li" :to="'/singerDetail/' + singer.Fsinger_mid" v-for="(singer) in singers.data" :key="singer.Fsinger_id">
                            <img v-lazy="'https://y.gtimg.cn/music/photo_new/T001R150x150M000'+ singer.Fsinger_mid +'.jpg?max_age=2592000'" alt="">
                            <span v-text="singer.Fsinger_name"></span>
                        </router-link>
                    </ol>
                </li>
            </ul>
        </div>

        <!-- S 索引tools -->
        <div class="index-tools" v-if="hasSinger" @mousemove="chooseIndex" @touchmove="chooseIndex" :style="autoFontSize">
            <a href="javascript:" :class="{'active': curCateIndex == index}" v-for="(key, index) in findexArr" :key="index" v-text="key.findex" :data-start="key.indexRange.start"></a>
        </div>
    </div>
</template>

<script>
// 载入接口
import API from '@/api/api'
import HTTP from '@/utils/http'

// 载入iscroll滚动插件
import IScroll from 'iscroll/build/iscroll-probe'
import {mapGetters} from 'vuex'

export default {
    data: function() {
        return {
            // 歌手数据
            singerList: [],
            // 歌手索引
            findexArr: [{
                findex: '热',
                indexRange: {
                    start: 0,
                    end: 0
                }
            }],
            // 当前分类索引
            curCateIndex: 0,
            // iscroll实例
            singerIScroll: null
        }
    },
    created() {
        this.getSingerList()
        .then(res => {
            let indexArr = this.getSingerFindex(res)
            this.formateSingerData(res, indexArr)
            this.calcSingerCateArea()

            setTimeout(() => {
                this.singerIScroll.refresh()
            }, 0)
        })
    },
    computed: {
        ...mapGetters({
            curSong: 'getCurSong'
        }),
        hasSinger: function() {
            return this.singerList.length > 0
        },
        // 是否有miniplayer在显示，动态调整滚动区域的高度
        autoHeight: function() {
            return this.curSong ? 'height: calc(100vh - 85px - 60px)' : 'height: calc(100vh - 85px)'
        },
        //  是否有miniplayer在显示，动态调整index-tools的font-size值
        autoFontSize: function() {
            return this.curSong ? 'font-size: 10px' : 'font-size: 12px'
        }
    },
    mounted() {
        this.singerIScroll = new IScroll('#singerScrollWrapper', {
            mouseWheel: true,
            useTransform: true,
            useTransition: true,
            disableMouse: true,
            disablePointer: true,
            disableTouch: false,
            tap: true,
            click: true,
            probeType: 3
        })

        this.singerIScroll.on('scroll', () => {
            let offsetY = Math.abs(this.singerIScroll.y)
            this.findexArr.forEach((indexItem, i) => {
                if (offsetY >= indexItem.indexRange.start && offsetY <= indexItem.indexRange.end) {
                    this.curCateIndex = i
                }
            })
        })
    },
    methods: {
        // 获取歌手列表
        getSingerList() {
            return new Promise((resolve, reject) => {
                HTTP.get(API.singer.singerList, {param: 'jsonpCallback'})
                .then(({ code, data: { list } }) => {
                    if (code == 0 && list && list.length > 0) {
                        resolve(list)
                    } else {
                        reject(new Error('获取歌手信息失败！'))
                    }
                })
            })
        },
        // 生成歌手索引
        getSingerFindex(list) {
            let indexArr = [...new Set(list.map((singer, index) => {
                                    return singer.Findex
                                }))].sort()

            indexArr.forEach((key, index) => {
                let tempRange = {
                    findex: key,
                    indexRange: {
                        start: 0,
                        end: 0
                    }
                }
                this.findexArr.push(tempRange)
            })

            return indexArr
        },
        // 生成歌手优化结构数据
        formateSingerData(list, indexArr) {
            let singerListTemp = [];
            // 添加一个热门索引歌手信息
            let hotSinger = {
                Findex: '热',
                data: list.slice(0, 10)
            }
            singerListTemp.push(hotSinger)

            // 添加其他索引对应的歌手信息
            indexArr.forEach((key, i) => {
                let tempObj = {
                    Findex: key,
                    data: list.filter((singer, index) => {
                        return singer.Findex == key
                    })
                }

                singerListTemp.push(tempObj)
            })
            
            this.singerList = singerListTemp
        },
        // 计算每个歌手分类区域所辖范围
        calcSingerCateArea() {
            this.singerList.forEach((singerCate, index) => {
                let rangeTemp = {start: 0, end: 0}
                if (index > 0) {
                    rangeTemp.start = this.findexArr[index - 1].indexRange.end;
                }


                let totalH = (30 + 20) + singerCate.data.length * 70
                rangeTemp.end = rangeTemp.start + totalH

                this.findexArr[index].indexRange = rangeTemp
            })
        },
        // 通过在indextool移动，动态切换到对应的歌手分类数据
        chooseIndex(e) {
            let target = e.target
            let start = target.getAttribute('data-start') * -1
            this.singerIScroll.scrollTo(0, start, 500)
        }
    },
    components: {
    }
}
</script>
<style scoped lang="scss">
@import 'SCSS/singer';
</style>